import {oilConsumption,driveDay} from "../../../api/user.js"
import {format} from "../../../util/util.js"
export default {
	data(){
		return {
			id:'' ,// 车id
			currObj:{},
			dayObj:{}
		}
	},
	onLoad(opts) {
		uni.hideShareMenu() 
		this.id = opts.carId
		this.getOil()
		this.getDayOil()
	},
	methods:{
		getOil(){
			oilConsumption({
				data:{
					car_id:this.id
				}
			}).then(res=>{
				if(res.data.code == 0) {
					this.currObj = res.data.result.drive_history
				}
			})
		},
		getDayOil(){
			driveDay({
				data:{
					car_id:this.id,
					time:format(new Date().getTime(),'day')
				}
			}).then(res=>{
				if(res.data.code == 0) {
					this.dayObj = res.data.result.drive_history_day
				}
			})
		}
	}
}
